+component(shop-page-product)

  //padding: 0.5em
  border: 5px $color-grey solid
  +border-radius(0.5em)

  +option(no-border)
    border: 0px

  background-color: white

  color: $color-black
  a
    color: $color-black

  h2
    margin: 0em
    margin-top: 0.5em
    font-size: 1.7em
    +truncate(95%)

  +part(discount)
    position: absolute
    left: 70%
    top: -10px
    width: 60px
    height: 60px
    padding: 0.8em
    color: $color-white
    background-color: $color-red
    z-index: 5
    +border-radius(50%)
    +rotate(20deg)
    div
      font-size: 18px
      margin-top: 0.3em
      font-weight: bold
      text-align: center

  +part(image)
    background-color: $color-white
    height: 17em
    width: 12em

    margin: 0 auto
    display: block

    // -webkit-background-size: cover
    // -moz-background-size: cover
    // -o-background-size: cover
    // background-size: cover

    -webkit-background-size: 100% auto
    -moz-background-size: 100% auto
    -o-background-size: 100% auto
    background-size: 100% auto

    background-repeat: no-repeat
    background-position: center center

    +breakpoint('mobile')
      width: 70%

    img
      max-width: 100%
      max-height: 22em

  +part(description)

    width: 95%
    margin: 0 auto
    margin-top: 0.3em
    margin-bottom: 1em

    +option(text)
      width: 100%
      font-size: 13px

    +option(prices)
      width: 100%

  +part(purchase)

    +option(like)
      z-index: 3
      color: $color-red
      min-height: 30px
      line-height: 1.5em
      text-align: center
      font-size: 20px

    +option(buy)
      z-index: 3
      color: $color-blue
      min-height: 30px
      line-height: 1.5em
      text-align: center
      font-size: 20px
